import * as React from 'react';
import PhotoCardTable from "./PhotoCardTable";

// const clientId = "cce224bce71147e58f349ff724933a49";
// const tokenRequestUrl= "https://api.instagram.com/oauth/authorize/?client_id=cce224bce71147e58f349ff724933a49&redirect_uri=http://localhost:3000&response_type=token";
const baseURL = 'https://api.instagram.com/v1/users/self/media/recent/'; // append accessToken for query

class InstaFeed extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            userData: []
        }
    }

    componentDidMount() {
        fetchData(baseURL+"?access_token="+this.props.accessToken)
            .then(userProfileData => this.readData(userProfileData))
            .catch(error => console.error(error))
    }

    // From the profile data, grab image data to and save to state
    readData(userProfileData) {
        const imagesData = [];

        userProfileData.data.forEach(function (data) {
            
            // Ensure only nicely formatted images are grabbed.
            const height = data.images.standard_resolution.height;
            const width = data.images.standard_resolution.width;
            const hwRatio = height/width;

            if (0.6 < hwRatio && hwRatio < 1 ){

                // Remove hashtags from captions
                let caption = data.caption.text;
                const hashtagIndex = caption.indexOf("#");
                const dotIndex = caption.indexOf(".")

                if (hashtagIndex !== -1) {
                    caption = caption.substring(0, hashtagIndex);
                    if (dotIndex !== -1) {
                        caption = caption.substring(0, dotIndex);
                    }
                }

                imagesData.push(
                    {
                        id: data.id,
                        url: data.images.standard_resolution.url,
                        caption: caption,
                        date: data.created_time
                    }
                );
            }
        });

        this.setState({
            userData: imagesData
        })
    }

    render() {
        return (
            <PhotoCardTable photos={this.state.userData} photoPerRow={this.props.photoPerRow}/>
        );
    }
}

export default InstaFeed;

///////

// Helper function
const fetchData = (url) => {
    return fetch(url)
        .then(data => data.json())
        .then(json => {
            if (json) {
                return Promise.resolve(json);
            } else {
                return Promise.reject(Error('json is undefined!'));
            }
        })
}


