import React from 'react';
import PhotoCard from './PhotoCard';
import { Container, Row, Col, CardDeck} from 'reactstrap';

class PhotoCardTable extends React.Component{

    render() {
        const photos = this.props.photos;

        // Fill the rows
        let photoRows = [];
        let lastRow = -1;
        let currentRow = 0;

        photos.forEach((photo, index) => {
            currentRow = Math.floor(index / this.props.photoPerRow);

            // Create new row to add new photocards if the previous row is full.
            if (currentRow !== lastRow) {
                photoRows.push([]);
                lastRow = currentRow;
            }

            photoRows[currentRow].push(
                <PhotoCard key = {photo.id} src = {photo.url} title ={photo.caption} date ={photo.date}/>
            );
        });

        let photoCardTable = [];

        photoRows.forEach((row, index) => {
            photoCardTable.push(
                <Row key = {index}>
                    <CardDeck className="mx-auto">
                        {row}
                    </CardDeck>
                </Row>
            );
        });

        return (
            <div className="photoCardTable">
                <Container>
                    <Col xs="12">
                        {photoCardTable}
                    </Col>
                </Container>
            </div>
        );
    }
}

export default PhotoCardTable;


