import React from 'react';
import {  Card, CardImg, CardBody,
    CardTitle, CardFooter} from 'reactstrap';

class PhotoCard extends React.Component {

    render() {
        const date = new Date(parseInt(this.props.date*1000,10));

        const month = (date.getMonth()+1);
        const day= date.getDate();
        const fullYear = date.getFullYear();

        const fullDate = "Posted on " + day + "/" + month + "/" + fullYear;

        const altText = "Image titled \"" + this.props.title + "\"";
        return(
            <Card>
                <CardImg top width="100%" src={this.props.src} alt={altText} />
                <CardBody>
                    <CardTitle className="text-center">
                        {this.props.title}
                    </CardTitle>
                </CardBody>
                <CardFooter>
                    <small className="text-muted">{fullDate}</small>
                </CardFooter>
            </Card>
        );
    }
}

export default PhotoCard;


