import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import InstaFeed from "./InstaFeed";

class App extends Component {
    render() {
        return (
            <div className="App">
                <header className="App-header">
                    <img src={logo} className="App-logo" alt="logo" />
                    <h1 className="App-title">Welcome to the Gallery</h1>
                </header>
                <body>
                    <InstaFeed accessToken="6290965111.cce224b.8ba5c28c54e5447d943b370cc167349f"
                        photoPerRow="2"/>
                </body>
            </div>
        );
    }
}

export default App;



